/* Colors */
.portalWidth {
  padding-left: 20px;
  padding-right: 20px;
  min-width: 400px; }

.framedImage {
  margin: 20px 0;
  border: 1px solid #eee !important;
  border-radius: 5px;
  padding: 10px;
  display: block;
  box-shadow: 1px 1px 1px 0px rgba(0, 0, 0, 0.21); }

.VAFChartControls {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 15px; }
  .VAFChartControls > label {
    font-weight: normal;
    display: flex;
    align-items: center;
    margin-left: 30px !important;
    margin-bottom: 0 !important;
    white-space: nowrap; }
