.patientPageHeader {
  position: relative;
  padding-left: 45px;
  display: flex; }
  .patientPageHeader .cohortNav {
    text-align: right; }
    .patientPageHeader .cohortNav .btn-group {
      float: right !important;
      display: flex; }
  .patientPageHeader td {
    vertical-align: top;
    padding-left: 10px;
    padding-bottom: 4px; }
  .patientPageHeader .studyMetaBar {
    text-align: right;
    flex-grow: 1; }
    .patientPageHeader .studyMetaBar .studyMetaBar_studyName {
      white-space: nowrap;
      max-width: 400px;
      text-overflow: ellipsis;
      display: inline-block;
      overflow: hidden; }
  .patientPageHeader .patientDataTable {
    flex-grow: 1; }
  .patientPageHeader .patientIcon {
    position: absolute;
    left: 0;
    font-size: 40px; }
  .patientPageHeader .patientSamples .patientSample {
    display: inline-block;
    background-color: #ddd;
    padding: 1px 2px;
    margin-right: 3px;
    border-radius: 3px;
    margin-bottom: 3px;
    font-size: 11px;
    height: 20px;
    line-height: 20px;
    vertical-align: middle; }
    .patientPageHeader .patientSamples .patientSample svg {
      position: relative;
      top: 1px; }

.vafPlot {
  position: relative;
  top: -10px; }

.resourcesTab .resourcesSection {
  padding: 10px;
  border-radius: 5px;
  border: 1px dashed #ddd;
  margin-bottom: 30px; }
  .resourcesTab .resourcesSection:last-child {
    margin-bottom: 0px; }

.patient-clinical-data-tab > div {
  margin-bottom: 20px; }
