/* Colors */
.portalWidth {
  padding-left: 20px;
  padding-right: 20px;
  min-width: 400px; }

.framedImage {
  margin: 20px 0;
  border: 1px solid #eee !important;
  border-radius: 5px;
  padding: 10px;
  display: block;
  box-shadow: 1px 1px 1px 0px rgba(0, 0, 0, 0.21); }

.axis path,
.axis line {
  fill: none;
  stroke: black;
  shape-rendering: crispEdges; }

.axis text {
  font-family: sans-serif;
  font-size: 10px; }

#timeline > svg:first-child .timeline-label:last-of-type {
  display: none; }

#timeline .axis {
  transform: translate(0px, 20px);
  -ms-transform: translate(0px, 20px);
  /* IE 9 */
  -webkit-transform: translate(0px, 20px);
  /* Safari and Chrome */
  -o-transform: translate(0px, 20px);
  /* Opera */
  -moz-transform: translate(0px, 20px);
  /* Firefox */ }

#timeline circle {
  cursor: pointer; }

#timeline g text {
  cursor: pointer; }

.extent {
  opacity: 0.1; }

.coloredDiv {
  height: 20px;
  width: 20px;
  float: left; }

.timeline-qtip {
  padding: 5px; }
  .timeline-qtip table tr td {
    padding-top: 5px;
    padding-left: 5px;
    padding-right: 5px; }
  .timeline-qtip table tr:first-child td {
    padding-top: 0; }

.timelineContainer {
  position: relative;
  display: inline-block;
  padding-right: 10px; }
