.tl-timeline-wrapper {
  position: relative;
  min-height: 200px; }
  .tl-timeline-wrapper .tl-timeline-reset-buttons {
    position: absolute;
    top: -25px;
    display: inline-block;
    left: 50%; }

.tl-timelineviewport {
  border-top: none;
  overflow-x: auto;
  overflow-y: hidden;
  display: inline-block;
  width: 1200px;
  flex-grow: 1; }

.tl-viewport-pseudo-border {
  width: 1px;
  background: #ccc; }

.tl-timeline {
  position: relative; }
  .tl-timeline .tl-cursor {
    position: absolute;
    top: 0;
    left: 0px;
    border-left: 1px dashed #999;
    width: 1px;
    height: 1000px;
    display: none;
    z-index: 10;
    overflow: visible;
    pointer-events: none; }
    .tl-timeline .tl-cursor > div {
      position: absolute;
      background: #999;
      color: #fff;
      padding: 1px;
      font-size: 9px;
      white-space: nowrap;
      width: 75px;
      top: 0px;
      left: -37.5px;
      text-align: center;
      opacity: 1;
      border-radius: 4px;
      display: none; }
  .tl-timeline .tl-track {
    pointer-events: all; }
    .tl-timeline .tl-track rect.tl-track-highlight {
      fill: #f2f2f2;
      opacity: 0; }

.tl-timeline-leftbar .tl-timeline-tracklabels {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  font-size: 12px;
  text-transform: uppercase; }
  .tl-timeline-leftbar .tl-timeline-tracklabels > div {
    white-space: nowrap;
    border-bottom: 1px dashed #eee;
    padding-right: 10px;
    padding-top: 2px; }

.tl-zoom-selectbox {
  position: absolute;
  height: 1000px;
  background: #ccc;
  left: 100px;
  z-index: 100;
  opacity: 0.3;
  display: none; }

.tl-zoom-selectbox-mask {
  position: absolute;
  z-index: 99;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: none; }

.tl-timeline-tooltip {
  max-width: 480px !important;
  min-width: 300px; }
  .tl-timeline-tooltip:hover {
    z-index: 1070 !important; }
  .tl-timeline-tooltip .popover-content {
    padding: 2px 5px !important;
    border: none;
    font-size: 10px; }
    .tl-timeline-tooltip .popover-content td,
    .tl-timeline-tooltip .popover-content th {
      padding: 1px 5px; }

.timeline-label:last-of-type {
  display: none; }

.tl-displaynone {
  display: none; }

.tl-hover-disabled {
  background: none !important; }
  .tl-hover-disabled .tl-track-highlight {
    display: none; }

.tl-custom-track-header {
  position: relative; }
